@import url('https://fonts.googleapis.com/css?family=Source+Code+Pro:300,400,500,600,700');
@import url('https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css');

html, body {
	margin: 0px;
}

html, body, input, select, button {
	color: #356a0e;
	font-weight: 300;
	font: 12px 'Source Code Pro', monospace;
}

h1 {
	text-align: center;
}

.body-wrapper {
	display: flex;
	flex-direction: row;
}

.body-wrapper > :first-child {
	flex-grow: 1;
}

.name-box {
	border: 0px;
	font-size: 20px;
	min-width: 400px;
	font-style: italic;
	outline: none;
	padding: 5px;
	line-height: 15px;
	border: 1px solid rgba(0,0,0,0);
	border-radius: 5px;
	margin: 10px;
}

.name-box::placeholder {
	color: #AAA;
	font-style: italic;
}

.name-box:hover {
	border: 1px solid #EEE;
}

.name-box:focus {
	border: 1px solid #DDD;
}

.sidebar-wrapper {
}

.sidebar {
	width: 200px;
	background-color: #d0ffd1;
	padding-left: 8px;
}

button.new {
	width: 100px;
	background-color: #FFF;
	font-size: 14px;
	margin: 20px;
	border: 0px;
}

.sidebar ul {
	list-style: none;
	padding: 2px;
}

.sidebar ul > li > button {
  border: 0px;
	outline: none;
	cursor: pointer;
	font-size: 1.2em;
	border-radius: 3px;
	max-width: 130px;
	overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.sidebar ul > li > button.icon {
	background-color: #d0ffd1;
	margin: 2px;
}

.table input {
	min-width: 20px;
  max-width: 87px;
	font: 12px 'Source Code Pro', monospace;
	font-weight: 300;
  text-align: right;
  border: 0px;
  background-color: #d0ffd1;
	color: #356a0e;
	outline: none;
}

.table input[type='checkbox'] {
	max-width: 12px;
}

.table input[type='number'] {
  -moz-appearance:textfield;
}

.table input[type=number]::-webkit-inner-spin-button,
.table input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.table select {
	-webkit-appearance: none;
  -moz-appearance: none;
	font-weight: 300;
  background-color: #d0ffd1;
	color: #356a0e;
  border: none;
  background-position: right 50%;
  background-repeat: no-repeat;
	background-image: url(data:image/png;base64,);
  padding: 0.2em 2em 0.2em 0.7em;
}

.table {
	display: table;
	with: 100%;
	background-color: #FFF;
}

.table .row {
	display: table-row;
}

.table .row.title > .cell {
  padding: 8px 4px;
  border-top: 8px solid #d0ffd1;
	font-style: italic;
	font-size: 1.2em;
	font-weight: 500;
}

.table .row.title > .cell.h {
	font-size: 1.5em;
	font-weight: 600;
	font-style: italic;
}

.table .cell {
	display: table-cell;
	padding: 3px 4px;
	text-align: right;
	border-right: 1px solid #d0ffd1;
}

.table .cell.-center {
	text-align: center;
}

.table .cell.h {
	font-weight: 500;
}

.cell > *:first-child {
  padding: 3px;
	border-radius: 4px;
}

.row.s > .cell {
	font-weight: 500;
	padding-bottom: 40px;
}

.row.h > .cell {
	padding-bottom: 40px;
	font-weight: 500;
}

.tooltip {
  position: fixed;
  font-style: italic;
  z-index: 1000;
  display: inline-block;
  max-width: 400px;
  margin-top: 10px;
  padding: 8px;
  font-size: 11px;
  font-weight: 500;
  line-height: 1.5em;
  color: #11a111;
  text-align: left;
  border: 1px solid #ebe3e3;
  border-radius: 4px;
  box-shadow: 3px 3px 8px -3px #CCC;
  background: #FFF;
  transform: scale(0);
  transform-origin: top center;
}

.tooltip.active {
  transform: scale(1);
}

.help-icon {
	text-align: center;
  width: 1.4em;
  height: 1.4em;
  line-height: 1.4em;
  color: white;
  background: #8ac85b;
	border-radius: 20px;
	font-size: 1em;
	font-weight: 700;
	display: inline-block;
}



